#resume {
  width: 21cm;
  height: 29.7cm;
}
/* body */

.resume-div {
  height: 80vh;
}

input:focus,
textarea:focus {
  outline: none;
}

header {
  height: 10vh;
}

.sidebar {
  width: 15vw;
  height: 90vh;
  /* bottom: 0; */
}
.sidebar-r{
  width: 50%;
  height: 90vh;
}

main {
  width: 85vw;
  height: fit-content;
  position: fixed;
  bottom: 0;
  right: 0;
  /* top: 0; */
}

.sidebar a.active, .sidebar-r a.active {
  color: #a78bfa;
  font-weight: 600;
  /* white-space: nowrap; */
}

@media print {
  #resume {
    /* padding: 0 !important; */
    height: 100vh !important;
    overflow: hidden !important;
    /* height: fit-content; */
    padding-top: 3rem !important;
    padding-bottom: 0 !important;
  }
}

.auth ::placeholder{
  color: #545151;
}

@media (max-width: 768px) {
  body{
    overflow-x: hidden;
  }
  main{
    height: 100vh;
    width: 100vw;
    position: relative !important;
    /* top: 4rem !important; */
    /* left: 2rem!important */
  }
}